@import '@/assets/styles/index.scss';





















.logo {
  display: block;
  padding: rem(22px);

  @include mq($from: 768px) {
    padding: rem(58px 0);
  }

  .modal & {
    padding: 0;
  }

  &__image {
    width: rem(28px);
    height: rem(28px);
    color: var(--primary-color);
  }
}
